
footer {
  text-align: center;
  padding: 2rem;
  font-size: 0.9rem;
  color: #8b949e;
  background-color: #0c0f1a;
  border-top: 1px solid #30363d;
}
@media (max-width: 768px) {
  /* NavBar */
  nav {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  nav ul {
    flex-direction: column;
    gap: 1rem;
    width: 100%;
  }

  /* Hero */
  .hero-inner {
    flex-direction: column;
    text-align: center;
  }

  .hero-left, .hero-right {
    min-width: unset;
    width: 100%;
  }

  .hero-name {
    font-size: 2.2rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  .profile-pic {
    width: 200px;
    height: 200px;
  }

  /* About Cards */
  .about-cards {
    flex-direction: column;
  }

  .info-card {
    padding: 1.5rem;
  }

  /* Projects */
  .project-grid {
    grid-template-columns: 1fr;
  }

  .project img {
    height: 160px;
  }

  /* Skills */
  .skills-wrapper {
    flex-direction: column;
  }

  /* Contact */
  .contact-content {
    flex-direction: column;
  }

  .contact-form, .contact-info-card {
    width: 100%;
  }

  /* Footer */
  footer {
    font-size: 0.8rem;
  }
}
