html {
  scroll-behavior: smooth;
}

/* Base NavBar Styles */
nav {
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: #0d1117;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 2rem;
  box-shadow: 0 0 10px rgba(88, 166, 255, 0.08);
}


/* Logo + Hamburger Container */
.nav-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

nav h1 {
  color: white;
  font-size: 1.4rem;
  font-weight: bold;
  letter-spacing: 1px;
}

/* Desktop Nav Links */
nav ul {
  list-style: none;
  display: flex;
  gap: 2rem;
}

nav a {
  color: #c9d1d9;
  font-weight: 600;
  font-size: 0.95rem;
  padding-bottom: 2px;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  transition: color 0.2s ease, border 0.2s ease;
}

nav a:hover {
  color: white;
  border-bottom: 2px solid transparent;
}

nav a.active {
  color: white;
  border-bottom: 2px solid #58a6ff;
}

/* Hamburger Menu */
.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  gap: 5px;
}

.bar {
  width: 25px;
  height: 3px;
  background-color: #c9d1d9;
  transition: all 0.3s ease;
}

/* Responsive Layout */
@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  nav ul {
    display: none;
  }

  .nav-links {
    display: none;
    flex-direction: column;
    width: 100%;
    gap: 1rem;
    margin-top: 1rem;
  }

  .nav-links.open {
    display: flex;
  }

  .nav-links li {
    width: 100%;
  }

  .nav-links a {
    display: block;
    width: 100%;
    padding: 0.75rem 0;
  }
}
