.contact {
  padding: 6rem 2rem;
  background-color:hsl(216, 28%, 7%);
}

.contact-title {
  text-align: center;
  font-size: 2.5rem;
  background: linear-gradient(90deg, #58a6ff, #c084fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact-subtitle {
  text-align: center;
  color: #b1b1b1;
  font-size: 1rem;
  margin-bottom: 2rem;
  position: relative;
}

.contact-subtitle::after {
  content: '';
  display: block;
  width: 80px;
  height: 3px;
  margin: 0.75rem auto 0;
  background: linear-gradient(90deg, #58a6ff, #c084fc);
  border-radius: 999px;
}

.contact-content {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  max-width: 1100px;
  margin: 0 auto;
  justify-content: center;
}

.contact-form {
  flex: 1 1 500px;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.form-row {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 0.9rem 1rem;
  background-color: #1e2532;
  border: 1px solid rgba(255, 255, 255, 0.07);
  color: #ffffff;
  font-size: 1rem;
  border-radius: 10px;
  outline: none;
}

.contact-form button {
  background: linear-gradient(90deg, #58a6ff, #c084fc);
  color: white;
  padding: 0.9rem;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.contact-form button:hover {
  opacity: 0.9;
}

.contact-info-card {
  flex: 1 1 300px;
  background-color: #1e2532;
  padding: 2rem;
  border-radius: 12px;
  color: #c9d1d9;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.contact-info-card h3 {
  color: white;
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}

.contact-info-card a {
  color: #58a6ff;
  text-decoration: none;
}

.contact-info-card a:hover {
  text-decoration: underline;
}

.social-icons {
  display: flex;
  gap: 1rem;
  font-size: 1.3rem;
}
@media (max-width: 768px) {
  /* NavBar */
  nav {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  nav ul {
    flex-direction: column;
    gap: 1rem;
    width: 100%;
  }

  /* Hero */
  .hero-inner {
    flex-direction: column;
    text-align: center;
  }

  .hero-left, .hero-right {
    min-width: unset;
    width: 100%;
  }

  .hero-name {
    font-size: 2.2rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  .profile-pic {
    width: 200px;
    height: 200px;
  }

  /* About Cards */
  .about-cards {
    flex-direction: column;
  }

  .info-card {
    padding: 1.5rem;
  }

  /* Projects */
  .project-grid {
    grid-template-columns: 1fr;
  }

  .project img {
    height: 160px;
  }

  /* Skills */
  .skills-wrapper {
    flex-direction: column;
  }

  /* Contact */
  .contact-content {
    flex-direction: column;
  }

  .contact-form, .contact-info-card {
    width: 100%;
  }

  /* Footer */
  footer {
    font-size: 0.8rem;
  }
}
