.hero {
  padding: 4rem 2rem;
  background-color: hsl(216, 28%, 7%);
}

.hero-inner {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
}

.hero-left {
  flex: 1;
  min-width: 300px;
}

.hero-name {
  font-size: 3.2rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
}

.gradient-text {
  background: linear-gradient(90deg, #58a6ff, #c084fc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero-title {
  font-size: 1.6rem;
  font-weight: 600;
  color: white;
  margin-bottom: 1rem;
}

.hero-description {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #c9d1d9;
  margin-bottom: 2rem;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.btn {
  padding: 0.75rem 1.6rem;
  font-weight: 600;
  border-radius: 8px;
  text-align: center;
  font-size: 1rem;
  transition: all 0.3s ease;
  text-decoration: none;
}

.btn-primary {
  background: linear-gradient(90deg, #58a6ff, #c084fc);
  color: white;
  box-shadow: 0 0 15px rgba(88, 166, 255, 0.4);
}

.btn-primary:hover {
  opacity: 0.9;
}

.btn-secondary {
  background-color: hsl(225.88deg 23.94% 13.92%);
  color: white;
  border: 1px solid #30363d;
}

.btn-secondary:hover {
  background-color: #21262d;
}

.hero-right {
  flex: 1;
  min-width: 250px;
  display: flex;
  justify-content: center;
}

.profile-pic {
  width: 260px;
  height: 260px;
  object-fit: cover;
  border-radius: 50%;
  border: 3px solid #58a6ff;
  box-shadow: 0 0 40px rgba(88, 166, 255, 0.3);
}
@media (max-width: 768px) {
  /* NavBar */
  nav {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  nav ul {
    flex-direction: column;
    gap: 1rem;
    width: 100%;
  }

  /* Hero */
  .hero-inner {
    flex-direction: column;
    text-align: center;
  }

  .hero-left, .hero-right {
    min-width: unset;
    width: 100%;
  }

  .hero-name {
    font-size: 2.2rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  .profile-pic {
    width: 200px;
    height: 200px;
  }

  /* About Cards */
  .about-cards {
    flex-direction: column;
  }

  .info-card {
    padding: 1.5rem;
  }

  /* Projects */
  .project-grid {
    grid-template-columns: 1fr;
  }

  .project img {
    height: 160px;
  }

  /* Skills */
  .skills-wrapper {
    flex-direction: column;
  }

  /* Contact */
  .contact-content {
    flex-direction: column;
  }

  .contact-form, .contact-info-card {
    width: 100%;
  }

  /* Footer */
  footer {
    font-size: 0.8rem;
  }
}
